@import url(https://cdn.jsdelivr.net/gh/aritmatica/cdn@main/fonts/satoshi/stylesheet.css);
@font-face {
  font-family: "Material Symbols Outlined";
  font-style: normal;
  font-weight: 100 700;
  src: url(https://cdn.jsdelivr.net/gh/aritmatica/cdn@main/gstatic/icons.woff2) format("woff2");
}
:root {
  color-scheme: dark;
}

.icons {
  font-family: "Material Symbols Outlined";
  font-weight: normal;
  font-style: normal;
  font-size: 1.25em;
  line-height: 0.79;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  vertical-align: top;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  font-feature-settings: "liga";
  -webkit-font-feature-settings: "liga";
  -webkit-font-smoothing: antialiased;
}

* {
  margin: 0;
  padding: 0;
  font-family: satoshi-regular, arial, sans-serif;
  box-sizing: border-box;
}

.fancy-button {
  position: relative;
  cursor: pointer;
  overflow: hidden;
  color: white;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.fancy-button .flair {
  position: absolute;
  background-color: white;
  aspect-ratio: 1;
  width: 200%;
  pointer-events: none;
  border-radius: 100%;
  will-change: transform;
  z-index: 0;
}
.fancy-button .label {
  font-family: inherit;
  z-index: 0;
  transition: color 0.2s;
  white-space: nowrap;
}
.fancy-button:hover {
  color: black;
}

body {
  color: #FFFFFF;
  background-color: #0e0f0f;
  overflow-x: hidden;
}

#home-container {
  padding: 12vw 1rem 0 1rem;
}

header, section {
  max-width: 72rem;
  margin: 0 auto 0 auto;
  display: flex;
  gap: 1vw;
}
header aside, section aside {
  width: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
header aside .icons, section aside .icons {
  font-size: 2.25rem;
  margin-top: 0;
  cursor: default;
}
header aside .line, section aside .line {
  width: 3px;
  border-radius: 3px;
  transform-origin: top;
}
header main, section main {
  width: 95%;
}

header aside .icons {
  color: gray;
  font-size: 0.75rem;
}
header aside .line {
  background-color: linear-gradient(transparent, #e36970 30%);
  background-image: linear-gradient(transparent, #e36970 30%);
  height: 100%;
}
header main {
  padding-bottom: 2rem;
}
header main h1 {
  white-space: nowrap;
  font-size: min(16vw, 12rem);
}
header main h1 sup {
  font-size: 0.5em;
}
header main h1 sup .glow {
  position: absolute;
  background-color: transparent;
  width: 0;
  border-radius: 100%;
  display: inline-block;
  opacity: 1;
  z-index: -1;
  box-shadow: 0 0 1.2em 0.7em #e36970;
}
header main span {
  color: gray;
  font-size: min(5vw, 2rem);
}

#intro aside .icons {
  height: 2rem;
  font-size: 2.5rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
#intro aside .icons .glow {
  width: 0;
  height: 0;
  box-shadow: 18px 18px 36px 18px #e36970;
}
#intro aside .line {
  background-color: linear-gradient(#e36970, #3fb950 80%);
  background-image: linear-gradient(#e36970, #3fb950 80%);
  height: 16rem;
}
#intro main .buttons {
  margin-top: 0.5rem;
  display: flex;
  flex-direction: row;
}
#intro main .buttons a {
  width: min-content;
  border: solid #FFFFFF;
  border-radius: 1.5rem;
  padding: 0.5rem 1rem 0.5rem 1rem;
  margin-right: 0.75rem;
  font-family: satoshi-bold, arial, sans-serif;
  font-size: 1.5rem;
  white-space: nowrap;
  text-align: center;
}
#intro main .buttons #intro-start {
  border-color: #e36970;
  color: #e36970;
}
#intro main .buttons #intro-start .flair {
  background-color: #e36970;
}
#intro main .buttons #intro-start:hover {
  color: #FFFFFF;
}
@media screen and (max-width: 768px) {
  #intro main .buttons {
    margin-left: 1rem;
    flex-direction: column;
    gap: 0.5rem;
  }
  #intro main .buttons a {
    width: 90%;
    padding: 0.25rem 0.5rem 0.25rem 0.5rem;
    font-family: satoshi-bold, arial, sans-serif;
    font-size: min(5vw, 1.5rem);
  }
}

#genmath aside .icons {
