#genmath main {
  margin-left: 1rem;
}
#genmath main h2, #genmath main h2 span {
  font-size: min(12vw, 4rem);
  font-family: satoshi-black, arial, sans-serif;
  margin-top: 1.125rem;
}
#genmath main h2 .highlight, #genmath main h2 span .highlight {
  color: transparent;
  background-image: linear-gradient(90deg, #3fb950, #3fb979);
  -webkit-background-clip: text;
  background-clip: text;
}
#genmath main h3, #genmath main h3 span {
  margin-top: 1rem;
  font-size: min(6vw, 2rem);
  font-family: satoshi-black, arial, sans-serif;
}
#genmath main .card {
  background-color: #191a1f;
  width: min(95vw, 76rem);
  height: 32rem;
  padding: min(8%, 4.5rem) min(8%, 4.5rem) min(8%, 4.5rem) min(8%, 4.5rem);
  margin: 3.5rem auto 0 auto;
  border-radius: 16px;
  position: absolute;
  left: 0;
  right: 0;
  overflow: hidden;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}
#genmath main .card .card-text .card-title {
  color: gray;
  font-size: 1.5rem;
  line-height: 1.25;
}
#genmath main .card .card-text .card-title span {
  color: white;
  font-size: inherit;
  line-height: inherit;
}
#genmath main .card .card-media {
  border-radius: 24px;
  grid-row: span 2;
  font-size: 1.5rem;
  position: relative;
  height: 20rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#genmath main .card .button-container {
  display: flex;
  align-items: end;
  font-size: 1.5rem;
}
#genmath main .card .button-container .fancy-button {
  font-size: 1em;
  font-family: satoshi-bold, arial, sans-serif;
  white-space: nowrap;
  border: solid #FFFFFF;
  border-radius: 1em;
  padding: 0.5em 1em 0.5em 1em;
  width: fit-content;
  height: fit-content;
}
@media screen and (max-width: 1024px) {
  #genmath main .card {
    height: fit-content;
    grid-template-columns: 1fr;
  }
  #genmath main .card .button-container {
    justify-content: center;
  }
}
#genmath #genmath-log {
  background-image: linear-gradient(#3fb950, #3fb983);
  -webkit-mask: url(https://cdn.jsdelivr.net/gh/aritmatica/cdn@main/svg/aritmatica-logarithm.svg) no-repeat center/cover;
  mask: url(https://cdn.jsdelivr.net/gh/aritmatica/cdn@main/svg/aritmatica-logarithm.svg) no-repeat center/cover;
  opacity: 0.01;
  position: absolute;
  width: 125%;
  height: 100%;
  transform: scale(-1, 1);
}

